@font-face {
  font-family: StarWars;
  src: url('./fonts/StarJedi-DGRW.ttf');
}


.App {
  height: 100vh;
  margin: auto;
  text-align: center;
  background-image: url('./images/deep-space.jpg');
  background-repeat: repeat;
  background-attachment: fixed;
  background-size: cover;
  -webkit-animation: backgroundScroll 3s ease-in-out infinite alternate;
  -moz-animation: backgroundScroll 3s ease-in-out infinite alternate;
  animation: backgroundScroll 3s ease-in-out infinite alternate;

}

@keyframes backgroundScroll {
  from {
    background-position: 0 0;
  }

  to {
    background-position: 0px -5px;
  }
}

h1,
h3 {
  font-family: StarWars;
}

h1 {
  font-size: 3rem;
  height: 90%;
}

h2 {
  font-size: 2rem;
  width: 75%;
  margin: 2rem auto;
  transition: 0.5s ease-in-out;
}

h3 {
  font-size: 2rem;
  height: 10%;
}

button {
  transition: 0.5s ease-in-out;
  font-size: 5rem;
  font-weight: bold;
  border: none;
  background-color: transparent;
  width: 100%;
  margin-bottom: 1rem;
}

button:hover:not(:disabled) {
  font-size: 5.1rem;
}

.Title {
  width: 100;
  height: 20vh;
  padding: 1vh;
}

.AppBody {
  transition: 0.5s ease-in-out;
  height: 85vh;
  border-radius: 50%/75px 75px 0 0;
  border-top: 1px solid rgba(255, 255, 255, 0.389);
}

.AppBody * {
  font-family: 'Gemunu Libre', sans-serif;
}

.Battle {
  width: 75%;
  height: 75%;
  margin: auto;
  padding: 2.5%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}

.NeutralText {
  color: #e3befbcb;
}

.Neutral {
  box-shadow: 0 -10px 100px 0 #b976efb6;
  background-color: #1e0c33f9;
}

.LightSideText {
  color: #a19ffecb;
}

button.LightSideText:hover {
  text-shadow: 0 0 0.3rem #1500ff, 0 0 0.1rem #1500ff, 0 0 0.2rem #ffffff;
}

.LightSideBar {
  background-color: #a19ffecb;
}

.LightSide {
  box-shadow: 0 -10px 100px 0 #7688efb6;
  background-color: #141650f9;
}

.DarkSideText {
  color: #f58585cb;
}

button.DarkSideText:hover {
  text-shadow: 0 0 0.3rem #ff0000, 0 0 0.1rem #ff0000, 0 0 0.2rem #ffffff;
}

.DarkSideBar {
  background-color: #f58585cb;
}

.DarkSide {
  box-shadow: 0 -10px 100px 0 #ef7676b6;
  background-color: #330c0cf9;
}


.Characters {
  width: 100%;
  display: flex;
  justify-content: space-around;
}

.Character {
  display: flex;
  flex-direction: column;
  width: 40%;
  justify-content: flex-start;
  align-items: center;
  min-height: 20rem;
}

.WinPctBar {
  color: white;
  width: 5rem;
  display: flex;
  align-items: center;
  justify-content: center;
}